import {React, useEffect} from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import useFetch3 from '../Requester/useFetch3';
import Post from './EventPost';

let Events = () => {
    const {data3, setData3} = useFetch3();

useEffect(() => {
        let m = "https://prifuk.onrender.com/posts/api";
              setData3({ ...data3, slug: m });    
    setTimeout(()=>{
        }, 100)
      
      }, [])
    
    return(
    
    <div style={{}}>
<div style={{width:'200px', marginLeft:'auto', marginRight:'auto'}}>
<img style={{width:'200px'}} src={logo}></img></div>
<Navbar></Navbar>
<div style={{width: '60%', marginLeft: 'auto', marginRight: 'auto'}}>
{!data3.results ? "Loading..." : 
              data3.results.slice(0).map(({ _id, caption, contentShort, content, section, date, imageURL}) =>
          <Post caption={caption} contentShort={contentShort} content={content} imageURL={imageURL} key={date} _id={_id}></Post>
            )}
</div></div>
)}

export default Events;